import React, { Suspense, useEffect, useState } from "react";
import DropDown from "../../../DropDown/DropDown";
import { GetAllProductCodes, allBomAssociatedwithATenant } from "../../../../api/graphQlQuery";
import { runMutation, runQuery } from "../../../../api/apiCalls";
import { DTX_GRAPHQL_URL } from "../../../../../config";
import { BOM_CREATE_SUCCESS, BOM_UNIQUE_ERROR, JWT_ERROR_MESSAGE } from "../../../../constFunctions/constants";
import { logout, showErrorMessage, successMessage } from "../../../../constFunctions/copmponentFunctions";
import { setNextPage } from "../../../../redux-toolkit/slice/TaskpaneSlice";
import { useDispatch, useSelector } from "react-redux";
import "./BomCreate.css";
import Button from "../../../Button/Button";
const AddNewMaterail = React.lazy(() => import("../../AddNewMaterail/AddNewMaterail"));
import {
  activateGivenSheet,
  createNewSheet,
  getAllsheetNames,
  getLastUsedRowNumber,
  writeHeadingsToExcel,
} from "../../../../constFunctions/excelFunction";
import { setBomName, setUploadLoader } from "../../../../redux-toolkit/slice/action";
import { CreateProductBomQuery, CreateProjectBomQuery } from "../../../../api/graphQlMutation";
import ProjectIndicator from "../../../ProjectIndicator/ProjectIndicator";

function BomCreate() {
  const [productCodes, setProductCodes] = useState([]);
  const [prodCodeLoading, setProdCodeLoading] = useState(false);
  const [selectedProdCode, setSelectedProdCode] = useState();
  const [addNewBomMaterial, setAddNewBomMaterial] = useState(false);
  const dispatch = useDispatch();
  const { bomMappingData, activeProject } = useSelector((state) => state?.taskpane);
  const { bomName } = useSelector((state) => state.action);

  // while rendering bom-create page this useEffect fetches  all product codes
  useEffect(() => {
    setProdCodeLoading(true);
    const token = localStorage.getItem("tenantToken");
    try {
      const body = {
        operationName: "GetAllProductCodes",
        query: GetAllProductCodes,
        variables: null,
      };
      runQuery(DTX_GRAPHQL_URL, token, body)
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            for (let i = 0; i < data?.data?.product_code?.length; i++) {
              setProductCodes((preState) => [
                ...preState,
                { label: data?.data?.product_code[i]?.product_code, value: data?.data?.product_code[i]?.id },
              ]);
            }
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
        });
    } catch (error) {
      console.log(error);
    } finally {
      setProdCodeLoading(false);
    }
  }, [true]);
  const handleProdCodeChange = (selectedProdCode) => {
    setSelectedProdCode(selectedProdCode);
  };
  // after checking the mapping config and bom name this function will paste selected material data to excel
  // if there is any associated bom , then it opens addnewMaterial modal (function for pasting such materails are provioded in that componnent )
  const handleCreateBom = async () => {
    try {
      if (
        bomMappingData.quantity &&
        bomMappingData.total_price &&
        bomMappingData.unit_size &&
        bomMappingData.id &&
        bomMappingData.custom_material_id
      ) {
        // function to check the given name is already taken or not for another bom ;
        await checkBomNames().then(async (nameExist) => {
          if (nameExist) {
            console.log(nameExist);
            showErrorMessage("bom-name-check-warning", `${bomName} is already taken`);
          } else {
            // crates a new sheet
            const AllSheetNames = await getAllsheetNames();
            if (AllSheetNames.includes(bomName)) {
              activateGivenSheet(bomName);
              Object.entries(bomMappingData)?.map(([key, value]) => {
                writeHeadingsToExcel(bomMappingData, key, bomName);
              });
            } else {
              let sheetname = await createNewSheet(bomName);
              activateGivenSheet(sheetname);
              Object.entries(bomMappingData)?.map(([key, value]) => {
                writeHeadingsToExcel(bomMappingData, key, sheetname);
              });
            }
            setAddNewBomMaterial(true);
          }
        });
      } else {
        document.getElementById("mappingWarning").style.display = "block";
        setTimeout(() => {
          document.getElementById("mappingWarning").style.display = "none";
        }, 4000);
      }
    } catch (error) {
      console.log(error);
    } finally {
      //   setBomLoading(false);
    }
  };

  // checkBomNames just ensures there is no same bom name in dtx
  const checkBomNames = () => {
    return new Promise((resolve, reject) => {
      try {
        const body = {
          operationName: "allBomAssociatedwithATenant",
          query: allBomAssociatedwithATenant,
        };
        runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
          .then((data) => {
            if (data.errors) {
              data.errors.map((element) => {
                if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                  logout();
                  dispatch(setNextPage("homeLogin"));
                }
              });
            } else {
              for (let i = 0; i < data?.data?.core_bom?.length; i++) {
                if (data?.data?.core_bom[i].name === bomName) {
                  resolve(true);
                  break;
                }
              }
              resolve(false);
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } catch (error) {
        console.log(error);
      }
    });
  };
  const handleUpdateNewBom = async () => {
    try {
      if ((!selectedProdCode?.value && activeProject?.id === "Master") || !bomName) {
        showErrorMessage("bom-name-check-warning", "check Bom name and selected prodcuct code ");
      } else {
        await checkBomNames().then(async (nameExist) => {
          if (nameExist) {
            showErrorMessage("bom-name-check-warning", `${bomName} is already taken`);
          } else {
            function bomDataConstructor(material_id, quantity, total_price, unit_size, associated_product_code_bom) {
              this.material_id = material_id;
              this.quantity = quantity;
              this.total_price = total_price;
              this.unit_size = unit_size;
              associated_product_code_bom ? (this.associated_product_code = associated_product_code_bom) : null;
            }

            if (
              bomMappingData.quantity &&
              bomMappingData.total_price &&
              bomMappingData.unit_size &&
              bomMappingData.id &&
              bomMappingData.custom_material_id
            ) {
              dispatch(setUploadLoader(true));
              const updatedBomItems = [];
              let lastRow = await getLastUsedRowNumber();
              for (let i = 2; i <= lastRow; i++) {
                const element = {};
                bomMappingData.id ? (element.material_id = await getUpdatedDataOfQuantity(i, "id")) : null;
                bomMappingData.quantity ? (element.quantity = await getUpdatedDataOfQuantity(i, "quantity")) : null;
                bomMappingData.total_price
                  ? (element.total_price = await getUpdatedDataOfQuantity(i, "total_price"))
                  : null;
                bomMappingData.unit_size ? (element.unit_size = await getUpdatedDataOfQuantity(i, "unit_size")) : null;

                bomMappingData.associated_product_code_bom
                  ? (element.associated_product_code_bom = await getUpdatedDataOfQuantity(
                      i,
                      "associated_product_code_bom"
                    ))
                  : null;
                bomMappingData?.custom_material_id
                  ? (element.custom_material_id = await getUpdatedDataOfQuantity(i, "custom_material_id", "string"))
                  : null;
                let flag = true;
                for (let i = 0; i < updatedBomItems.length; i++) {
                  if (
                    updatedBomItems[i].custom_material_id === element.custom_material_id &&
                    updatedBomItems[i].unit_size === element.unit_size
                  ) {
                    updatedBomItems[i].quantity = updatedBomItems[i].quantity + element.quantity;
                    flag = false;
                  }
                }
                if (flag) {
                  updatedBomItems.push(element);
                }
              }
              const bom_items = updatedBomItems?.map((bomItem) => {
                let bomData = new bomDataConstructor(
                  bomItem.material_id,
                  bomItem?.quantity,
                  bomItem?.total_price,
                  bomItem?.unit_size,
                  bomItem.associated_product_code_bom
                );
                return bomData;
              });
              const token =
                activeProject?.id === "Master"
                  ? localStorage.getItem("tenantToken")
                  : localStorage.getItem("projectExchangeToken");

              const body =
                activeProject?.id === "Master"
                  ? {
                      operationName: "",
                      variables: {
                        product_code_id: selectedProdCode?.value,
                        name: bomName,
                        items: bom_items,
                      },
                      query: CreateProductBomQuery,
                    }
                  : {
                      operationName: "CreateProjectBomQuery",
                      variables: {
                        name: bomName,
                        items: bom_items,
                      },
                      query: CreateProjectBomQuery,
                    };
              runMutation(DTX_GRAPHQL_URL, token, body)
                .then((data) => {
                  if (data?.errors) {
                    data.errors.map((element) => {
                      if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                        logout();
                        dispatch(setNextPage("homeLogin"));
                      } else {
                        showErrorMessage("bom-name-check-warning", "Something went wrong");
                      }
                    });
                  } else if (data?.data?.insert_bom?.message?.includes(BOM_CREATE_SUCCESS)) {
                    console.log("inside bom creatiuiomn");
                    successMessage("bomUpdate-success", "Bom created Successfully!");
                  } else {
                    showErrorMessage("bom-name-check-warning", "Something went wrong");
                  }
                })
                .catch((error) => {
                  dispatch(setUploadLoader(false));
                  console.log("Error", error);
                  showErrorMessage("bom-name-check-warning", "Something went wrong");
                });
            }
            // else works if lack of required mapping config
            else {
              document.getElementById("mappingWarning").style.display = "block";
              setTimeout(() => {
                document.getElementById("mappingWarning").style.display = "none";
              }, 4000);
            }
          }
        });
      }
    } catch (error) {
      console.log(error);
      dispatch(setUploadLoader(false));
      showErrorMessage("bom-name-check-warning", "Something went wrong");
    } finally {
      dispatch(setUploadLoader(false));
    }
  };
  // getUpdatedDataOfQuantity thsi function fetches each cel value and returns a number if type is not passed ,if type passed then it returns a string
  const getUpdatedDataOfQuantity = async (i, field, type) => {
    try {
      return await Excel.run(async (context) => {
        let value = context.workbook.worksheets
          .getActiveWorksheet()
          .getRange(String(bomMappingData[field]?.address + i))
          .load("values");
        await context.sync();
        if (type) {
          return String(value.values[0][0]);
        } else {
          if (typeof value.values[0][0] !== "number") return 1;
          else return value.values[0][0];
        }
      });
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div className=" bom-create-container">
      <ProjectIndicator />
      {activeProject?.id === "Master" && (
        <div>
          <DropDown
            options={productCodes}
            handleChange={handleProdCodeChange}
            value={selectedProdCode}
            label={"Selelct the Product Code"}
            noOptionMessage={"No Product Codes availble"}
            prodCodeLoading={prodCodeLoading}
          />
        </div>
      )}
      <div className="bom-addname-container">
        <label htmlFor=""> Enter the bom name </label>
        <input
          type="text"
          className="bom-addName-input"
          value={bomName}
          placeholder="New Bom name..."
          onChange={(e) => dispatch(setBomName(e.target.value))}
        />
      </div>
      <div className="addmaterial-button-bom">
        <span onClick={handleCreateBom}>
          {" "}
          <Button label="Add materials" enabled={addNewBomMaterial}/>
        </span>
        <span onClick={handleUpdateNewBom}>
          {" "}
          <Button label="Create " />
        </span>
      </div>

      <blockquote id="bom-name-check-warning" className="bom-name-check-warning"></blockquote>
      <blockquote id="bomUpdate-success" className="bomUpdate-success"></blockquote>
      <ul className="mappingWarning" id="mappingWarning">
        {!bomMappingData?.id && <li>ID</li>}
        {!bomMappingData?.unit_size && <li>Unit Size (BOM)</li>}
        {!bomMappingData?.quantity && <li>Quantity (BOM)</li>}
        {!bomMappingData?.total_price && <li>Total Price (BOM)</li>}
        {!bomMappingData?.custom_material_id && <li>Material Id</li>}
        <li>you have to map given mandatory field(s)</li>
      </ul>
      {addNewBomMaterial && (
        <Suspense fallback={<div></div>}>
          <AddNewMaterail setAddNewBomMaterial={setAddNewBomMaterial} />
        </Suspense>
      )}
    </div>
  );
}

export default BomCreate;
